import { Component, OnInit } from '@angular/core';
import { Concept } from '../../../Model/concept';
import { Technology } from '../../../Model/techology';
import { FbTechnologyService } from '../../../services/fb-technology.service';
import { FbConceptService } from '../../../services/fb-concept.service';

@Component({
  selector: 'app-add-concept',
  templateUrl: './add-concept.component.html',
  styleUrls: ['./add-concept.component.css']
})
export class AddConceptComponent implements OnInit {

  newConcept: Concept = 
  {
    ConceptQuestion:
    {
      Question:"",
      Answer:""
    }
  };

  technologies: Technology[];

  constructor(public fbTechnologyService:FbTechnologyService, public fbConceptService:FbConceptService) { 
    
  }

  ngOnInit() {
    this.fbTechnologyService.getTechnologies().subscribe(techs => this.technologies = techs);
  }
  techChange(id)
  {
    //alert(id);
    this.newConcept.TechnologyId = id;
  }

  AddConecpt()
  {
    if(this.newConcept.TechnologyId != "" && this.newConcept.ConceptQuestion.Question != "" && this.newConcept.ConceptQuestion.Answer != "")
    {
      this.fbConceptService.AddConcept(this.newConcept)
    }
  }
}



