import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Concept } from '../Model/concept';
import { Observable } from 'rxjs/Observable';
import { retry } from 'rxjs/operators/retry';

@Injectable()
export class FbConceptService {
  conceptCollection: AngularFirestoreCollection<Concept>;
  concepts: Observable<Concept[]>;
  conceptDoc: AngularFirestoreDocument<Concept>;

  constructor(public afs: AngularFirestore) {
    this.conceptCollection = this.afs.collection("Concept");
    this.concepts = this.conceptCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Concept;
        data.Id = a.payload.doc.id;
        return data;
      });
    });
  }

  GetConcepts() {
    return this.concepts;
  }

  GetConceptsByTechnology(technologyId) {
    //alert(technologyId);
    this.conceptCollection = this.afs.collection("Concept", ref => ref.where('TechnologyId', '==', technologyId));
    this.concepts = this.conceptCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Concept;
        data.Id = a.payload.doc.id;
        return data;
      });
    });
    return this.concepts;
  }
  GetConceptsCountByTechnology(technologyId) {
    //alert(technologyId);
    
    this.conceptCollection = this.afs.collection("Concept", ref => ref.where('TechnologyId', '==', technologyId));
    return this.conceptCollection.valueChanges();
    // console.log(conceptCount);
    // return conceptCount;
  }

  AddConcept(concept: Concept) {
    this.conceptCollection.add(concept);
  }

  UpdateConcept(concept: Concept) {
    this.conceptDoc = this.afs.doc(`Concept/${concept.Id}`);
    this.conceptDoc.update(concept);
  }
  DeleteConcept(concept: Concept) {
    this.conceptDoc = this.afs.doc(`Concept/${concept.Id}`);
    this.conceptDoc.delete();
  }
}


